import fetch from 'isomorphic-fetch';
import * as types from '../constants/actionTypes';
import { getApiUri } from '../utils';
import apiFetch from './apiFetch';
import URI from 'URIjs';
import jsonHeaders from './jsonHeaders';
import jsonHeadersWithToken from './jsonHeadersWithToken';

export function checkToken(token) {
  return function (dispatch) {
    let resolvedToken = resolveToken(token);
    return fetch(`${getApiUri()}/users/is-valid`,
      {
        method: 'GET',
        headers: {'Authorization': `Bearer ${resolvedToken}`}
      }).then(response => {
      return response.json();
    }).then(json => {
      if (json && json.error) {
        localStorage.removeItem('token');
        dispatch(setIfUserIsLoggedIn(false));
      } else {
        dispatch(setIfUserIsLoggedIn(json));
      }
      return json;
    })
  }
}

export function login(username, password) {
  return function(dispatch) {
    var uri = URI(getApiUri()).segment('login');
    const loginRequest = {username, password};
    return apiFetch(dispatch, uri, {
      method: 'POST',
      headers: jsonHeaders,
      body: JSON.stringify(loginRequest)
    }).then(response => {
      if (response.status === 200) {
      return response.headers.get('Authorization').replace('Bearer ','');
      } else {
        throw Error("Login failed");
      }
    });
  }
}

export function signUp(fullName, username, password) {
  return function(dispatch) {
    var uri = URI(getApiUri()).segment('users').segment('sign-up');
    const signUpRequest = {fullName, username, password};
    return apiFetch(dispatch, uri, {
      method: 'POST',
      headers: jsonHeaders,
      body: JSON.stringify(signUpRequest)
    }).then(response => {
      if (response.status === 200) {
      return response.status;
      } else {
        throw Error("Signup failed");
      }
    });
  }
}

export function updateFullnameOfSelf(fullname) {
  return function(dispatch) {
    var uri = URI(getApiUri()).segment('users').segment('self/full-name');
    return apiFetch(dispatch, uri, {
      method: 'POST',
      body: fullname,
      headers: jsonHeadersWithToken(resolveToken(''))
    }).then(response => {
      if (response.ok)
      dispatch(updateFullname(fullname));
    });
  }
}

export function getSelfDetails() {
  return function(dispatch) {
    var uri = URI(getApiUri()).segment('users').segment('self');
    let resolvedToken = resolveToken('');
    return apiFetch(dispatch, uri, {
      method: 'GET',
      headers: jsonHeadersWithToken(resolvedToken),
      credentials: 'same-origin'
    }).then(response => {
      return response.json();
    }).then(json => {
      dispatch(setSelfUser(json));
    });
  }
}

export function setSelfUser(user) {
  return function (dispatch) {
    // thunks allow for pre-processing actions, calling apis, and dispatching multiple actions
    // in this case at this point we could call a service that would persist the fuel savings
    return dispatch({
      type: types.SET_SELF_USER_DATA,
      payload: user
    });
  };
}

function resolveToken(token) {
  if (token !== '') {
    localStorage.setItem('token', token);
    return token;
  }
  return localStorage.getItem('token');
}


// example of a thunk using the redux-thunk middleware
export function setIfUserIsLoggedIn(isUserLoggedIn) {
  return function (dispatch) {
    // thunks allow for pre-processing actions, calling apis, and dispatching multiple actions
    // in this case at this point we could call a service that would persist the fuel savings
    return dispatch({
      type: types.IS_USER_LOGGED_IN,
      payload: isUserLoggedIn
    });
  };
}


export function updateFullname(fullname) {
  return function (dispatch) {
    // thunks allow for pre-processing actions, calling apis, and dispatching multiple actions
    // in this case at this point we could call a service that would persist the fuel savings
    return dispatch({
      type: types.UPDATE_FULLNAME,
      payload: fullname
    });
  };
}