import { SET_SELF_USER_DATA, UPDATE_FULLNAME } from '../constants/actionTypes';
import initialState from './initialState';

export default function selfUserReducer(state = initialState.self, action) {
  switch (action.type) {
    case SET_SELF_USER_DATA:
      return action.payload;
    case UPDATE_FULLNAME: {
      return Object.assign({}, state, {fullName: action.payload});
    }
    default:
      return state;
  }
}
