import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from "prop-types";
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { bindActionCreators } from 'redux';
import { withRouter } from "react-router-dom";
import * as actions from './../actions/userLoginActions';

const styles = {
    root: {
        flexGrow: 1,
    },
    grow: {
        flexGrow: 1,
    },
    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    },
};

class NavBar extends React.Component {

    constructor(props) {
        super(props);
        props.actions.getSelfDetails();
        this.state = {
            anchorEl: null,
            showEditProfile: false,
            fullname: null
        };
        this.handleMenu = this.handleMenu.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleCloseDialog = this.handleCloseDialog.bind(this);

        this.handleLogOut = this.handleLogOut.bind(this);
        this.handleProfileClick = this.handleProfileClick.bind(this);
        this.renderDialog = this.renderDialog.bind(this);
        this.handleTextInputChange = this.handleTextInputChange.bind(this);
    }


    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.fullname != prevState.fullname) {
            return { fullname: nextProps.fullname };
        }
        return null;
    }

    renderDialog() {
        return (<Dialog
            open={this.state.showEditProfile}
            onClose={this.handleCloseDialog}
            aria-labelledby="form-dialog-title"
        >
            <DialogTitle id="form-dialog-title">Edit your profile</DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    label="Full name"
                    name="fullname"
                    type="fname"
                    value={this.state.fullname}
                    onChange={this.handleTextInputChange}
                    fullWidth
                />
            </DialogContent>
        </Dialog>);
    }

    handleTextInputChange(event) {
        this.props.actions.updateFullnameOfSelf(event.target.value);
    }

    handleMenu(event) {
        this.setState({ anchorEl: event.currentTarget });
    }

    handleProfileClick() {
        this.setState({ showEditProfile: true });
    }

    handleCloseDialog() {
        this.setState({ showEditProfile: false });
    }

    handleClose() {
        this.setState({ anchorEl: null });
    }

    handleLogOut() {
        this.setState({ anchorEl: null });
        localStorage.removeItem('token');
        this.props.actions.setIfUserIsLoggedIn(false).then(() => {
            this.props.history.push('/');
        });
    }

    render() {
        const { classes } = this.props;
        const { anchorEl } = this.state;
        const open = Boolean(anchorEl);
        const dialog = this.renderDialog();
        if (!this.props.isUserLoggedIn) {
            return null;
        }
        return (<div>
            {dialog}
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="title" color="inherit" className={classes.grow}>
                        Money-Bunny
                    </Typography>
                    <div>
                        <IconButton
                            aria-owns={open ? 'menu-appbar' : null}
                            aria-haspopup="true"
                            onClick={this.handleMenu}
                            color="inherit"
                        >
                            <AccountCircle />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorEl}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            open={open}
                            onClose={this.handleClose}
                        >
                            <MenuItem onClick={this.handleProfileClick}>Profile</MenuItem>
                            <MenuItem onClick={this.handleLogOut}>Log Out</MenuItem>
                        </Menu>
                    </div>
                </Toolbar>
            </AppBar>
        </div>);

    }
}

NavBar.propTypes = {
    isUserLoggedIn: PropTypes.bool.isRequired,
    adminActions: PropTypes.object,
    users: PropTypes.array,
    classes: PropTypes.object,
    actions: PropTypes.object,
    history: PropTypes.object
};


function mapStateToProps(state) {
    return {
        isUserLoggedIn: state.isUserLoggedIn,
        fullname: state.self.fullName
    };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators(actions, dispatch)
    };
}

export default withRouter(withStyles(styles)(connect(
    mapStateToProps,
    mapDispatchToProps
)(NavBar)));
